<template>
  <div class="list">
    <div class="mui-content">
      <div class="mui-row">
        <div class="mui-col-xs-6" v-for="item in list" :key="item.id">
          <router-link :to="{
            path:'/info',
            query:{id:item.id}
          }">
            <li class="mui-table-view-cell text">
              <img :src="item.image" alt />
              <p style="text-align:left">{{item.name}}</p>
              <div>
                <span style="text-align:left">${{item.price}}</span>
                <b class="text-right">剩余{{item.num}}件</b>
              </div>
            </li>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "List",
  data() {
    return {
      list: null
    };
  },
  mounted() {
    this.$axios
      .get(
        "http://47.93.224.9/api/goodslist?last_id=0&category_id=" +
          this.$route.query.id
      )
      .then(res => {
        this.list = res.data.data;
      });
  }
};
</script>


<style>
* {
  padding: 0;
  margin: 0;
}
.list li {
  list-style: none;
}
.list > div {
  text-align: left;
}
.list .mui-row img {
  width: 145px;
  height: 145px;
}
.list a .text{
  text-align: center;
  background:#ffffff;
  margin: 5px;
  border-radius: 1rem;
}
.list a .text span {
  display: inline;
  font-size: 14px;
  color: red;
  float: left;
  padding-top: 0;
}
.list a .text .text-right {
  float: right;
  padding-left: 10px;
  font: normal;
  color: #cccccc;
}
</style>